.simple_modal
  background-color: $bg-color
  margin: 0
  position: relative
  width: 100vw
  height: 100vh
  overflow: hidden
  .mfp-close
    top: 5%
    right: calc(5% - 35px)
    font-size: 20px
    line-height: 20px
    opacity: 1
    @media (max-width: 1199px)
      top: 30px
      right: 60px
    @media (max-width: 575px)
      right: 20px
      top: 20px
      transform: scale(.755)
    .icon
      position: relative
      z-index: -1
    .close-line
      position: absolute
      top: calc(-50% + 5px)
      left: calc(-50% + 12px)
      z-index: -1
    &:hover
      .close-line
        path
          animation: link-arrow .5s cubic-bezier(0.47, 0, 0.745, 0.715) 0s both
  .modal-content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    .modal-image
      flex: 0 0 calc(50% - 10px)
      max-width: calc(50% - 10px)
      height: 100vh
      overflow: hidden
      @media (max-width: 959px)
        flex: 0 0 35%
        max-width: 35%
      @media (max-width: 575px)
        display: none
      .modal-bg
        display: block
        min-width: 100%
        min-height: 100%
        margin: 0
        object-fit: cover
    .modal-descr
      display: flex
      align-items: center
      justify-content: center
      flex-direction: column
      flex: 0 0 calc(50% + 10px)
      max-width: calc(50% + 10px)
      @media (max-width: 959px)
        flex: 0 0 65%
        max-width: 65%
      @media (max-width: 575px)
        flex: 0 0 100%
        max-width: 100%
      .descr-wrapper
        max-width: 436px
        margin: 0px auto 0 calc(20% - 20px)
        padding: 100px 0
        @media (max-height: 767px)
          padding: 50px 0
        @media (max-height: 660px)
          padding: 50px 20px 50px 0
          overflow: scroll
          max-height: 100vh
          max-width: 460px
        @media (max-width: 1199px)
          max-width: calc(100% - 60px)
          margin: 0 auto
        @media (max-width: 575px)
          max-width: calc(100% - 30px)
      .modal-title
        font-family: Oswald, sans-serif
        font-weight: 300
        font-size: 50px
        line-height: 120%
        @media (max-width: 767px)
          font-size: 40px
        @media (max-width: 575px)
          font-size: 35px
      .modal-txt
        margin-top: 60px
        @media (max-width: 767px)
          margin-top: 30px
      .modal-form
        margin-top: 60px
        @media (max-width: 767px)
          margin-top: 30px
        .input-group
          margin-top: 20px
          &:nth-of-type(1)
            margin-top: 0
          .input-style
            background-color: rgba(#000, .1)
            color: $txt-color
            @include input-placeholder
              color: $txt-color
          &.check-group
            margin-top: 35px
            label
              input:checked
                & + .label
                  &:before
                    color: $txt-color
              .label
                @media (max-width: 767px)
                  font-size: 14px
                &:before
                  border-color: $txt-color
          &.btn-group
            margin-top: 60px
            padding-left: 15px
            button
              color: $txt-color

