.circle-info .circle-text {
  animation: spin;
  animation-iteration-count: infinite;
  animation-duration: 12s;
  animation-timing-function: linear;
  transform-origin: center;
}

.input-group .input-style {
  display: block;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid transparent;
  border-radius: 45px;
  font-size: 14px;
  line-height: 22px;
  color: #fff;
  padding: 20px 30px;
}
@media (max-width: 959px) {
  .input-group .input-style {
    padding: 15px 30px;
  }
}
.input-group .input-style.placeholder {
  color: #fff;
}
.input-group .input-style::-webkit-input-placeholder {
  color: #fff;
}
.input-group .input-style::-moz-placeholder {
  color: #fff;
}
.input-group .input-style:-moz-placeholder {
  color: #fff;
}
.input-group .input-style:-ms-input-placeholder {
  color: #fff;
}
.input-group.check-group label {
  display: block;
}
.input-group.check-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  visibility: hidden;
  opacity: 0;
}
.input-group.check-group label input:checked + .label:before {
  color: #fff;
}
.input-group.check-group label .label {
  display: block;
  padding-left: 40px;
  position: relative;
  font-size: 16px;
  line-height: 20px;
}
.input-group.check-group label .label a {
  display: inline-block;
  text-decoration: none;
}
.input-group.check-group label .label:before {
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  width: 20px;
  height: 20px;
  border-radius: 3px;
  border: 1px solid #fff;
  color: transparent;
  content: "\e801";
  font-size: 12px;
  line-height: 12px;
  position: absolute;
  transition: color 0.3s;
  top: 0;
  left: 0;
}
.input-group.btn-group button {
  color: #fff;
  display: block;
  position: relative;
  background-color: transparent;
  border: none;
  padding: 20px 48px;
  z-index: 1;
}
@media (max-width: 1199px) {
  .input-group.btn-group button {
    padding: 20px 25px;
  }
}
.input-group.btn-group button .link-line {
  position: absolute;
  top: -10px;
  left: -7px;
  width: calc(100% + 14px);
  height: calc(100% + 14px);
}
.input-group.btn-group button:hover .link-line path {
  animation: link-arrow 0.5s cubic-bezier(0.47, 0, 0.745, 0.715) 0s both;
}

