.events-section__content .events-slider {
  position: relative;
  height: 850px;
  overflow: hidden;
}
.events-section__content .events-slider .wheel {
  position: absolute;
  top: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 7500px;
  height: 7500px;
  cursor: grab;
  left: 50%;
  transform: translateX(-50%);
}
.events-section__content .events-slider .wheel__card {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 436px;
  cursor: pointer;
}
.events-section__content .events-slider .wheel__card .image-block {
  overflow: hidden;
  position: relative;
}
.events-section__content .events-slider .wheel__card .image-block:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #2B211F;
  transform: translateY(100%);
  transition: transform 0.8s;
  transition-delay: 0.5s;
}
.events-section__content .events-slider .wheel__card .image-block .item-image {
  display: block;
  width: auto;
  max-width: 100%;
  margin: 0;
  position: relative;
  transition: opacity 0.5s;
  transition-delay: 1.5s;
  opacity: 0;
}
.events-section__content .events-slider .wheel__card .descr-block {
  opacity: 0;
  transition: opacity 0.3s;
  transition-delay: 1.5s;
  margin-top: 30px;
  color: #212121;
}
.events-section__content .events-slider .wheel__card.custom-animate .image-block:before {
  transform: translateY(0);
}
