.restaurant-events__content .events-slider {
  position: relative;
  height: 850px;
  overflow: hidden;
}
.restaurant-events__content .events-slider .wheel {
  position: absolute;
  top: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 7500px;
  height: 7500px;
  cursor: grab;
  left: 50%;
  transform: translateX(-50%);
}
.restaurant-events__content .events-slider .wheel__card {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 436px;
  cursor: pointer;
}
.restaurant-events__content .events-slider .wheel__card .image-block {
  overflow: hidden;
  position: relative;
}
.restaurant-events__content .events-slider .wheel__card .image-block:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #2B211F;
  transform: translateY(100%);
  transition: transform 0.8s;
  transition-delay: 0.5s;
}
.restaurant-events__content .events-slider .wheel__card .image-block .item-image {
  display: block;
  width: auto;
  max-width: 100%;
  margin: 0;
  position: relative;
  transition: opacity 0.5s;
  transition-delay: 1.5s;
  opacity: 0;
}
.restaurant-events__content .events-slider .wheel__card .descr-block {
  opacity: 0;
  transition: opacity 0.3s;
  transition-delay: 1.5s;
  margin-top: 30px;
  color: #212121;
}
.restaurant-events__content .events-slider .wheel__card.custom-animate .image-block:before {
  transform: translateY(0);
}
.restaurant-events__content .events-slider .wheel__card.custom-animate .image-block .item-image {
  opacity: 1;
}
.restaurant-events__content .events-slider .wheel__card.custom-animate .descr-block {
  opacity: 1;
}
.restaurant-events__content .events-slider .card-content {
  width: 100%;
  pointer-events: none;
  z-index: 999;
  cursor: pointer;
  position: absolute;
  will-change: transform;
}
