/* import animates here */
@-ms-keyframes spin
  from
    -ms-transform: rotate(0deg)
  to
    -ms-transform: rotate(360deg)
@-moz-keyframes spin
  from
    -moz-transform: rotate(0deg)
  to
    -moz-transform: rotate(360deg)
@-webkit-keyframes spin
  from
    -webkit-transform: rotate(0deg)
  to
    -webkit-transform: rotate(360deg)
@keyframes spin
  from
    transform: rotate(0deg)
  to
    transform: rotate(360deg)


@-webkit-keyframes link-arrow
  0%
    stroke-dashoffset: 392.6724548339844px
    stroke-dasharray: 392.6724548339844px

  100%
    stroke-dashoffset: 0
    stroke-dasharray: 392.6724548339844px

@keyframes link-arrow
  0%
    stroke-dashoffset: 392.6724548339844px
    stroke-dasharray: 392.6724548339844px

  100%
    stroke-dashoffset: 0
    stroke-dasharray: 392.6724548339844px
@import "wowjs"
@import "fade_in_up"
@import "fade_in_left"
@import "fade_in"
@import "fade_in_right"