.footer
  margin-top: 180px
  background-color: $footer-bg
  padding: 150px 0 60px
  @media (max-width: 1439px)
    margin-top: 150px
    padding: 105px 0 60px
  @media (max-width: 1199px)
    padding: 105px 0 30px
  @media (max-width: 959px)
    margin-top: 70px
    padding: 80px 0 30px
  @media (max-width: 575px)
    margin-top: 95px
    padding: 60px 0 30px
  &__content
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
    @media (max-width: 1439px)
      align-items: flex-end
    @media (max-width: 1199px)
      align-items: flex-start
    @media (max-width: 639px)
      align-items: center
    &__column
      flex: 0 0 auto
      max-width: 25%
      margin: 0 auto 0 0
      @media (max-width: 1199px)
        max-width: 270px
      @media (max-width: 959px)
        margin: 0
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
      @media (max-width: 575px)
        flex: 0 0 100%
        max-width: 100%
      .column-title
        display: block
        color: rgba(#fff, .5)
      .column-list
        margin-top: 60px
        @media (max-width: 1439px)
          margin-top: 30px
        @media (max-width: 1199px)
          margin-top: 50px
        @media (max-width: 959px)
          margin-top: 45px
        @media (max-width: 575px)
          margin-top: 15px
        &__item
          display: flex
          align-items: center
          justify-content: flex-start
          margin-top: 20px
          @media (max-width: 1199px)
            margin-top: 15px
          &:nth-of-type(1)
            margin-top: 0
          .name
            display: inline-block
            font-family: Oswald, sans-serif
            font-weight: 300
            font-size: 20px
            line-height: 20px
            color: #fff
            margin-right: 35px
            @media (max-width: 1439px)
              margin-right: 20px
            @media (max-width: 1199px)
              font-size: 18px
              min-width: 130px
              margin-right: 45px
            @media (max-width: 959px)
              flex: 0 0 auto
              width: auto
              font-size: 16px
              margin-right: 15px
          .social-list
            display: flex
            align-items: center
            justify-content: flex-start
            margin-left: auto
            @media (max-width: 959px)
              margin-left: 0
            &__item
              flex: 0 0 auto
              width: auto
              margin: 0 0 0 15px
              &:nth-of-type(1)
                margin-left: 0
            &__link
              display: inline-flex
              align-items: center
              justify-content: center
              flex: 0 0 50%
              width: 50px
              height: 50px
              border-radius: 50%
              background-color: rgba(#fff, .1)
              color: #fff
              font-size: 20px
              line-height: 20px
              transition: background-color .3s
              @media (max-width: 1199px)
                width: 40px
                height: 40px
                font-size: 16px
                line-height: 16px
              &:hover
                background-color: rgba(#fff, .2)
      &:nth-of-type(4n)
        margin-right: 0
      &:nth-of-type(2n)
        @media (max-width: 1199px)
          margin-right: calc(5% - 5px)
        @media (max-width: 959px)
          margin-left: 20px
          margin-right: 0
        @media (max-width: 575px)
          margin: 0 0 30px
      &:last-child
        @media (max-width: 575px)
          margin-bottom: 0 !important
      &.main-column
        @media (max-width: 1335px)
          flex: 0 0 100%
          max-width: 100%
          display: flex
          align-items: center
          justify-content: flex-start
          margin-bottom: 75px
        @media (max-width: 1199px)
          display: block
          flex: 0 0 50%
          max-width: 50%
          order: -1
          margin-bottom: 60px
        @media (max-width: 959px)
          flex: 0 0 calc(50% - 10px)
          max-width: calc(50% - 10px)
        @media (max-width: 575px)
          flex: 0 0 100%
          max-width: 100%
        .logo-row
          display: flex
          align-items: flex-start
          justify-content: flex-start
          @media (max-width: 1439px)
            flex-wrap: wrap
          @media (max-width: 1335px)
            align-items: center
            flex: 0 0 400px
            max-width: 400px
          @media (max-width: 959px)
            align-items: flex-start
          .logo-link
            display: block
            flex: 0 0 70px
            max-width: 70px
            margin-top: 10px
            @media (max-width: 1335px)
              margin: 0
            @media (max-width: 1199px)
              max-width: 58px
            @media (max-width: 959px)
              margin-top: 5px
            .logo-image
              display: block
              width: auto
              max-width: 100%
              margin: 0
          .logo-descr
            flex: 0 0 calc(100% - 70px)
            max-width: calc(100% - 70px)
            color: rgba(#fff, .5)
            padding-left: 50px
            @media (max-width: 1439px)
              flex: 0 0 100%
              max-width: 100%
              padding-left: 0
              line-height: 22px
              margin-top: 20px
            @media (max-width: 1335px)
              flex: 0 0 auto
              max-width: calc(100% - 70px)
              padding: 0 0 0 20px
              margin: 0
            @media (max-width: 1199px)
              padding: 0 0 0 35px
              line-height: 25px
            @media (max-width: 959px)
              padding: 0 0 0 25px
            @media (max-width: 639px)
              flex: 0 0 100%
              max-width: 100%
              padding: 10px 0 0
              font-size: 13px
              line-height: 20px
            @media (max-width: 575px)
              flex: 0 0 calc(100% - 70px)
              max-width: calc(100% - 70px)
              font-size: 14px
              line-height: 25px
              padding: 0 0 0 22px
        .search-form
          display: flex
          align-items: stretch
          justify-content: flex-start
          max-width: 436px
          position: relative
          margin: 60px 0
          @media (max-width: 1439px)
            margin: 30px 0 60px
          @media (max-width: 1335px)
            margin: 0 0 0 45px
            width: 100%
            max-width: 360px
          @media (max-width: 1199px)
            margin: 40px 0 0
            max-width: 450px
          @media (max-width: 959px)
            max-width: 313px
            margin: 15px 0 0
          @media (max-width: 575px)
            max-width: 100%
          input
            color: #fff
            padding: 22px 76px 22px 30px
            font-size: 14px
            line-height: normal
            flex: 0 0 100%
            max-width: 100%
            background-color: rgba(#fff, .1)
            border: 1px solid transparent
            border-radius: 45px
            transition: border-color .3s
            @media (max-width: 1199px)
              padding: 22px 100px 22px 20px
            @media (max-width: 959px)
              padding: 20px 70px 20px 20px
              font-size: 12px
            @include input-placeholder
              color: #fff
            &.validate-error
              border: 1px solid #FF6767
              + .required-label
                display: block
          button
            display: flex
            align-items: center
            justify-content: center
            background-color: transparent
            border: none
            color: #fff
            font-size: 26px
            line-height: 26px
            padding: 0 20px
            transition: color .3s
            position: absolute
            top: 0
            right: 0
            height: 100%
            @media (max-width: 1199px)
              font-size: 20px
              line-height: 20px
              padding: 0 35px
            @media (max-width: 959px)
              padding: 0 20px
            &:hover
              color: $accent-color
          .required-label
            display: none
            position: absolute
            bottom: -30px
            left: 0
            color: #FF6767
            font-size: 14px
            line-height: 14px
            padding: 0 30px
      &.delivery-column
        @media (max-width: 1199px)
          order: 0
        .social-list
          &__link
            background-color: $accent-color
            &:hover
              background-color: #ad4123
      &.delivery-column,
      &.social-column,
      &.answers-column
        margin-bottom: 70px
        @media (max-width: 1335px)
          margin-bottom: 40px
  &__bottom
    display: flex
    align-items: center
    justify-content: flex-start
    flex-wrap: wrap
    margin-top: 45px
    color: rgba(#fff, .5)
    @media (max-width: 1439px)
      margin-top: 15px
    @media (max-width: 575px)
      margin-top: 35px
      flex-direction: column
      align-items: flex-start
    .copyright
      display: inline-block
    .work
      display: inline-block
      margin-left:  auto
      @media (max-width: 1199px)
        margin-right: 30px
      @media (max-width: 639px)
        margin-right: 0
      @media (max-width: 575px)
        margin: 15px 0 0
      a
        display: inline-block
        text-decoration: none
        transition: color .3s
        &:hover
          color: $accent-color
  .scrollbtn
    position: fixed
    bottom: 30px
    right: 30px
    z-index: 20
    @media (max-width: 959px)
      transform: scale(.755)
      right: 20px
      bottom: 20px
    @media (max-width: 575px)
      display: none !important
  &.restaurant-footer
    .footer__content
      @media (max-width: 1439px)
        align-items: flex-start
      &__column
        @media (max-width: 1335px)
          margin-bottom: 40px
        .column-list
          margin-top: 0
        &.main-column
          .logo-row
            @media (max-width: 1335px)
              max-width: 500px
              flex: 0 0 500px
            .logo-link
              max-width: 90px
              flex: 0 0 90px
            .logo-descr
              padding-left: 32px
              @media (max-width: 1439px)
                flex: 0 0 100%
                max-width: 100%
                padding-left: 0
                line-height: 22px
                margin-top: 20px
              @media (max-width: 1335px)
                flex: 0 0 auto
                max-width: calc(100% - 90px)
                padding: 0 0 0 20px
                margin: 0
              @media (max-width: 1199px)
                padding: 0 0 0 35px
                line-height: 25px
              @media (max-width: 959px)
                padding: 0 0 0 25px
              @media (max-width: 767px)
                flex: 0 0 100%
                max-width: 100%
                padding: 20px 0 0 0
                font-size: 13px
                line-height: 20px
              @media (max-width: 639px)
                flex: 0 0 100%
                max-width: 100%
                padding: 10px 0 0
              @media (max-width: 575px)
                flex: 0 0 calc(100% - 90px)
                max-width: calc(100% - 90px)
                font-size: 14px
                line-height: 25px
                padding: 0 0 0 22px
        &.links-column
          @media (max-width: 959px)
            max-width: 240px
            flex: 0 0 240px
            margin: 0 0 0 auto
          @media (max-width: 575px)
            margin: 0 0 40px 0
          .column-list
            &__item
              .name
                font-family: var(--font-family)
                font-size: 24px
                line-height: 24px
                @media (max-width: 1199px)
                  font-size: 18px
            .social-list
              &__link
                &.active
                  background-color: #BC8548
                  &:hover
                    background-color: rgba(#BC8548, .8)
        &.time-column
          .time-list
            &__item
              display: flex
              align-items: center
              justify-content: flex-start
              margin-top: 14px
              @media (max-width: 575px)
                margin-top: 8px
              span
                flex: 1 1 auto
                width: auto
                &.item-name
                  color: rgba(#fff, .5)
                  font-size: 16px
                  line-height: 16px
                  min-width: 50px
                  margin-right: 60px
                  @media (max-width: 1439px)
                    margin-right: 30px
                  @media (max-width: 1199px)
                    min-width: 100px
                    font-size: 14px
                  @media (max-width: 575px)
                    min-width: 115px
                &.item-time
                  color: #fff
                  font-family: var(--font-family)
                  font-size: 24px
                  line-height: 24px
                  @media (max-width: 1199px)
                    font-size: 18px
        &.adress-column
          color: #fff
          padding-top: 12px
          text-align: right
          @media (max-width: 1335px)
            max-width: 30%
            flex: 0 0 30%
          @media (max-width: 959px)
            flex: 0 0 calc(50% - 10px)
            max-width: calc(50% - 10px)
          @media (max-width: 575px)
            flex: 0 0 100%
            max-width: 100%
            text-align: left
          .adress
            font-family: var(--font-family)
            font-size: 24px
            line-height: 24px
            @media (max-width: 1199px)
              font-size: 18px
            @media (max-width: 575px)
              font-size: 16px
          .phone-link
            display: inline-block
            margin-top: 55px
            color: #fff
            font-family: var(--font-family)
            font-size: 24px
            line-height: 24px
            transition: color .3s
            @media (max-width: 1199px)
              font-size: 18px
              margin-top: 22px
            @media (max-width: 575px)
              font-size: 16px
              margin-top: 12px
            &:hover
              color: $accent-color





