@import "../settings"
.section-title
  font-family: Oswald, sans-serif
  font-weight: 300
  font-size: 70px
  line-height: 120%
  @media (max-width: 1439px)
    font-size: 62px
  @media (max-width: 1199px)
    font-size: 40px
  @media (max-width: 959px)
    font-size: 35px
  @media (max-width: 359px)
    font-size: 27px
.circle-info
  .circle-text
    animation: spin
    animation-iteration-count: infinite
    animation-duration: 12s
    animation-timing-function: linear
    transform-origin: center
.input-group
  .input-style
    display: block
    width: 100%
    background-color: rgba(#fff, .1)
    border: 1px solid transparent
    border-radius: 45px
    font-size: 14px
    line-height: 22px
    color: #fff
    padding: 20px 30px
    @media (max-width: 959px)
      padding: 15px 30px
    @include input-placeholder
      color: #fff
  &.check-group
    label
      display: block
      input
        position: absolute
        top: 0
        left: 0
        width: 0
        height: 0
        visibility: hidden
        opacity: 0
        &:checked
          & + .label
            &:before
              color: #fff
      .label
        display: block
        padding-left: 40px
        position: relative
        font-size: 16px
        line-height: 20px
        a
          display: inline-block
          text-decoration: none
        &:before
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: flex
          align-items: center
          justify-content: center
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          width: 20px
          height: 20px
          border-radius: 3px
          border: 1px solid #fff
          color: transparent
          content: '\e801'
          font-size: 12px
          line-height: 12px
          position: absolute
          transition: color .3s
          top: 0
          left: 0
  &.btn-group
    button
      color: #fff
      display: block
      position: relative
      background-color: transparent
      border: none
      padding: 20px 48px
      z-index: 1
      @media (max-width: 1199px)
        padding: 20px 25px
      .link-line
        position: absolute
        top: -10px
        left: -7px
        width: calc(100% + 14px)
        height: calc(100% + 14px)
      &:hover
        .link-line
          path
            animation: link-arrow .5s cubic-bezier(0.47, 0, 0.745, 0.715) 0s both

