@import "settings"
@import "_mixins/_mixins"
@import "_components/ui"

/* main page */
.news-section
  padding-top: 200px
  @media (max-width: 1439px)
    padding-top: 150px
  @media (max-width: 959px)
    padding-top: 170px
  @media (max-width: 639px)
    padding-top: 100px
  &__title
    @media (max-width: 959px)
      flex: 0 0 calc(50% - 10px)
      max-width: calc(50% - 10px)
    @media (max-width: 639px)
      flex: 0 0 100%
      max-width: 100%
  &__content
    &__title-block
      max-width: 500px
      margin: 0 calc(22% - 5px) 0 auto
      position: relative
      @media (max-width: 1199px)
        max-width: calc(50% - 15px)
        margin: 0 0 0 auto
      @media (max-width: 959px)
        display: flex
        align-items: flex-start
        justify-content: flex-start
        flex-wrap: wrap
        max-width: 100%
        margin: 0
      .circle-wrapper
        position: absolute
        right: calc(100% + 175px)
        bottom: -25px
        @media (max-width: 1199px)
          right: calc(100% + 30px)
          bottom: -42px
          transform: scale(.75)
        @media (max-width: 959px)
          display: none
      .descr-block
        margin-top: 20px
        max-width: 420px
        @media (max-width: 959px)
          flex: 0 0 calc(50% - 10px)
          max-width: calc(50% - 10px)
          margin: 0 0 0 auto
          padding-top: 10px
        @media (max-width: 639px)
          flex: 0 0 100%
          max-width: 100%
          margin: 20px 0 0
          padding: 0
  .news-slider
    margin-top: 60px
    padding: 0 calc((100vw - $container-width + 25px) / 2)
    @media (max-width: 1850px)
      padding: 0 0 0 15px
    @media (max-width: 1199px)
      margin-top: 50px
      padding: 0 47px
    @media (max-width: 969px)
      margin-top: 40px
      padding: 0 61px
    @media (max-width: 767px)
      padding: 0 20px
    @media (max-width: 639px)
      padding: 0 15px
    .slide-item
      @media (max-width: 575px)
        max-width: 313px
      .image-block
        overflow: hidden
        position: relative
        &:before
          content: ''
          display: block
          width: 100%
          height: 100%
          position: absolute
          top: 0
          left: 0
          background-color: $footer-bg
          transform: translateY(100%)
          transition: transform .8s
          transition-delay: .5s
        .item-link
          display: block
        .item-image
          display: block
          width: auto
          max-width: 100%
          margin: 0
          position: relative
          opacity: 0
          transition: opacity .8s
          transition-delay: 1.5s
      .descr-block
        margin-top: 32px
        opacity: 0
        transition: opacity .3s
        transition-delay: 1.5s
        @media (max-width: 1199px)
          margin-top: 22px
        @media (max-width: 959px)
          margin-top: 18px
        .date
          display: block
          color: $accent-color
          font-size: 14px
          line-height: normal
          @media (max-width: 1199px)
            font-size: 12px
          span
            display: inline-flex
            &:nth-of-type(2)
              position: relative
              &:before
                content: '/'
                display: inline-block
                margin: 0 17px 0 12px
                @media (max-width: 1199px)
                  margin: 0 15px 0 10px
        .description
          margin-top: 24px
          @media (max-width: 1199px)
            margin-top: 18px
          @media (max-width: 969px)
            margin-top: 12px
        .more-link
          display: inline-flex
          align-items: center
          justify-content: flex-start
          color: $accent-color
          margin-top: 20px
          @media (max-width: 1199px)
            margin-top: 15px
          @media (max-width: 959px)
            margin-top: 10px
          .icon
            margin-left: 22px
            font-size: 22px
            line-height: 22px
            transition: transform .3s
            @media (max-width: 1199px)
              font-size: 16px
              line-height: 16px
              margin-left: 15px
          &:hover
            .icon
              transform: translateX(5px)
      &.custom-animate
        .image-block
          &:before
            transform: translateY(0)
          .item-image
            opacity: 1
        .descr-block
          opacity: 1
    .swiper-pagination
      display: none
      @media (max-width: 959px)
        display: block
        position: relative
        margin-top: 30px
        height: 1px
        background-color: #212121
        .swiper-pagination-progressbar-fill
          background-color: $accent-color
