    .events-slider
      position: relative
      height: 850px
      overflow: hidden
      .wheel
        position: absolute
        top: 25px
        display: flex
        align-items: center
        justify-content: center
        width: 7500px
        height: 7500px
        cursor: grab
        // max-width: 8000px
        // max-height: 8000px
        left: 50%
        transform: translateX(-50%)

      .wheel__card
        position: absolute
        top: 0
        left: 0
        width: 100%
        max-width: 436px
        cursor: pointer
        .image-block
          overflow: hidden
          position: relative
          &:before
            content: ''
            display: block
            width: 100%
            height: 100%
            position: absolute
            top: 0
            left: 0
            background-color: $footer-bg
            transform: translateY(100%)
            transition: transform .8s
            transition-delay: .5s
          .item-image
            display: block
            width: auto
            max-width: 100%
            margin: 0
            position: relative
            transition: opacity .5s
            transition-delay: 1.5s
            opacity: 0
        .descr-block
          opacity: 0
          transition: opacity .3s
          transition-delay: 1.5s
          margin-top: 30px
          color: $title-color
        &.custom-animate
          .image-block
            &:before
              transform: translateY(0)
            .item-image
              opacity: 1
          .descr-block
            opacity: 1

      .card-content
        width: 100%
        pointer-events: none
        z-index: 999
        cursor: pointer
        position: absolute
        will-change: transform

  &__btn-row
    display: flex
    align-items: center
    justify-content: center
    position: relative
    z-index: 1
    .events-btn
      color: $accent-color
      position: relative
      padding: 20px 48px
      z-index: 1
      .link-line
        position: absolute
        top: -10px
        left: -7px
        width: calc(100% + 14px)
        height: calc(100% + 14px)
        @media (max-width: 1199px)
          left: 8px
          width: calc(100% - 15px)
          height: calc(100% + 5px)
      &:hover
        .link-line
          path
            animation: link-arrow .5s cubic-bezier(0.47, 0, 0.745, 0.715) 0s both


/* restaurant page */
.restaurant-page
  .main-page__content
    &__left-side
      @media (max-width: 1600px)
        flex: 0 0 985px
        max-width: 985px
      @media (max-width: 1199px)
        flex: 0 0 50%
        max-width: 50%
      @media (max-width: 959px)
        flex: 0 0 100%
        max-width: 560px

.restaurant-descr
  padding-top: 200px
  @media (max-width: 1439px)
    padding-top: 150px
  @media (max-width: 959px)
