      padding-top: 122px
      @media (max-width: 1600px)
        padding-top: 0
      @media (max-width: 1199px)
        flex: 0 0 calc(55% + 10px)
        max-width: calc(55% + 10px)
      @media (max-width: 959px)
        flex: 0 0 80%
        max-width: 80%
      @media (max-width: 639px)
        flex: 0 0 100%
        max-width: 100%
      .descr-block
        margin-top: 62px
        max-width: 695px
        font-size: 16px
        line-height: 180%
        padding-right: 80px
        @media (max-width: 1439px)
          margin-top: 20px
        @media (max-width: 1199px)
          font-size: 14px
          line-height: 22px
        @media (max-width: 959px)
          max-width: 225px
          padding-right: 0
          margin-top: 38px
        @media (max-width: 639px)
          max-width: 100%
        b, strong
          display: block
          font-family: var(--font-family)
          font-weight: 400
          font-size: 30px
          line-height: 120%
          margin-bottom: 60px
          @media (max-width: 1439px)
            margin-bottom: 20px
          @media (max-width: 1199px)
            font-size: 24px
            margin-top: 30px
          @media (max-width: 959px)
            font-size: 20px
            line-height: 24px
            margin-bottom: 40px
            width: 135%
          @media (max-width: 639px)
            width: 100%
    &__right-side
      flex: 0 0 calc(55% - 20px)
      max-width: calc(55% - 20px)
      position: relative
      @media (max-width: 1199px)
        flex: 0 0 calc(45% - 10px)
        max-width: calc(45% - 10px)
      @media (max-width: 959px)
        flex: 0 0 20%
        max-width: 20%
      @media (max-width: 639px)
        flex: 0 0 100%
        max-width: 100%
        margin-top: 30px
      .small-slider
        max-width: 432px
        position: absolute
        z-index: 2
        bottom: 0
        left: -70px
        @media (max-width: 1439px)
          left: -40px
        @media (max-width: 1335px)
          max-width: 335px
        @media (max-width: 1199px)
          max-width: 185px
        @media (max-width: 959px)
          left: calc(-100% - 125px)
          bottom: 0
        @media (max-width: 767px)
          left: calc(-100% - 100px)
        @media (max-width: 639px)
          left: 0
          bottom: 0
          width: 45%
          max-width: 45%
        .slide-item
          .item-image
            display: block
            width: auto
            max-width: 100%
            margin: 0
      .big-slider
        width: 85%
        max-width: 760px
        margin: 0 0 100px auto
        @media (max-width: 1199px)
          max-width: 328px
        @media (max-width: 959px)
          position: absolute
          max-width: 313px
          width: 313px
          top: 155px
          right: 0px
        @media (max-width: 767px)
          width: 270px
          max-width: 270px
        @media (max-width: 639px)
          position: relative
          top: 0
          left: 0
          right: 0
          width: 65%
          max-width: 65%
          margin: 0 0 40px auto
        .slide-item
          .item-image
            display: block
            width: auto
            max-width: 100%
            margin: 0
      .circle-wrapper
        position: absolute
        bottom: -35px
        left: 446px
        @media (max-width: 1335px)
          bottom: -65px
        @media (max-width: 1199px)
          bottom: 260px
          left: -40px
          transform: scale(.755)
        @media (max-width: 959px)
          display: none

